/* make variables */
:root {
  --lightgray: #D9D9D9;
  --gray: #9c9c9c;
  --orange: #f48915;
  --darkGrey: #464D53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --appColor:  #3c3f45;
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}
::-webkit-scrollbar{
  display: none;
}

.stroke_text {
  color: transparent;
  -webkit-text-stroke: 2px white;
  font-family: Arial, Helvetica, sans-serif;
}

.btn {
  background-color: white;
  border: 5px solid transparent;
  font-weight: bold;
  padding: 0.5rem;
  transition: 300s all;
}

.btn:hover {
  cursor: pointer;
}
.blur {
  background-color: rgba(248, 101, 16, 0.75);
  position: absolute;
  border-radius: 50%;
  filter: blur(150px);
  z-index: -9;
}