.header {
    display: flex;
    justify-content: space-between;
}

.logo {
    width: 10rem;
    height: 3rem;
}

.header_menu {
    list-style-type: none;
    gap :2rem;
    display: flex;
    color: white;
}

.header_menu > li:hover {
    cursor: pointer;
    color: var(--orange);
}

@media screen and (max-width: 768px) {
    .header > :nth-child(2){
        position: fixed;
        right: 2rem;
        z-index: 99;
    }
    .header_menu {
        flex-direction: column;
        background-color: var(--appColor);
        padding: 2rem;
    }
}